import { Fab, Grid } from "@material-ui/core";
import { Visibility, VisibilityOff } from "@material-ui/icons";
import React, { useEffect, useState } from "react";
import { useDispatch } from "react-redux";
import { requestUserData } from "../redux/actions/actions";
import PortfolioForm from "./PortfolioForm";
import PortfolioPreview from "./PortfolioPreview";
const LandingPage = () => {
  const [preview, setPreview] = useState(false);
  const dispatch = useDispatch()
  const handlePreview = () => {
    setPreview  (!preview);
  };
  useEffect(()=>{
    dispatch(requestUserData())
  },[])
  return (
    <>
      <div className="position-relative">
        <textarea id="copy-text" style={{opacity:"0",height:'0px'}} />
        {!preview ? <PortfolioForm /> : <PortfolioPreview />}
        <div
          className="position-fixed"
          style={{ bottom: "20px", right: "50px" }}
        >
          <Fab
            variant="extended"
            size="medium"
            color="primary"
            aria-label="add"
            onClick={handlePreview}
          >
            {!preview ? <Visibility /> : <VisibilityOff />}
            <span className="mx-2">
              {!preview ? "Preview" : "Hide Preview"}
            </span>
          </Fab>
        </div>
      </div>
    </>
  );
};

export default LandingPage;
