import React, { useState } from "react";
import { Button, Grid } from "@material-ui/core";
import DeleteIcon from "@material-ui/icons/Delete";
import AddSkill from "./AddSkill";
import { Save } from "@material-ui/icons";
import { useDispatch, useSelector } from "react-redux";
import { requestSaveGeneralSkills } from "../../redux/actions/actions";

const GeneralSkillForm = () => {
  const { generalSkills } = useSelector((state) => state.user.data.resume);
  const dispatch = useDispatch();
  const [skills, setSkills] = useState(generalSkills);
  const removeSkill = (index) => {
    let temp = [...skills];
    temp.splice(index, 1);
    setSkills([...temp]);
  };
  const saveSkills = () => {
    dispatch(requestSaveGeneralSkills(skills));
  };
  return (
    <>
      <Grid
        container
        direction="row"
        justify="space-around"
        alignItems="flex-start"
        item
        xs={11}
        className="mx-auto"
        id="port-skills"
      >
        <Grid item xs={11} md={6} className="p-3 my-3 border rounded ">

        <Grid item xs={12} className="h2 my-3">
          General Skills
        </Grid>
        <Grid item xs={12}>
          {skills.length ? (
            <table className="table table-sm small">
              <thead>
                <tr>
                  <th>Skill Name</th>
                  <th style={{ textAlign: "center" }}>Grade</th>
                  <th style={{ textAlign: "right" }}>Remove Skill</th>
                </tr>
              </thead>
              <tbody>
                {skills.map(({ name, grade }, index) => (
                  <tr key={index}>
                    <td>{name}</td>
                    <td style={{ textAlign: "center" }}>{grade}</td>
                    <td style={{ textAlign: "right" }}>
                      {" "}
                      <Button
                        className="bg-danger text-white fw-bold"
                        size="small"
                        onClick={() => {
                          removeSkill(index);
                        }}
                        startIcon={<DeleteIcon />}
                      >
                        Delete
                      </Button>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          ) : (
            <p className="text-center my-2">Add some Skills to get good impact.</p>
          )}
        </Grid>
        <Grid
          container
          item
          direction="column"
          justify="flex-end"
          alignItems="flex-end"
          xs={12}
        >
          <AddSkill skills={skills} setSkills={setSkills} />
          {skills.length ? (
            <Grid
              item
              xs={12}
              style={{ textAlign: "right" }}
              className="mt-4"
            >
              <Button
                color="primary"
                variant="contained"
                size="small"
                startIcon={<Save />}
                onClick={saveSkills}
              >
                Save
              </Button>
            </Grid>
          ) : (
            <></>
          )}
        </Grid>
        </Grid>
      </Grid>
    </>
  );
};
export default GeneralSkillForm;
