import {
  Button,
  FormControl,
  Grid,
  IconButton,
  Tooltip,
} from "@material-ui/core";
import DescriptionIcon from "@material-ui/icons/Description";
import DeleteIcon from "@material-ui/icons/Delete";
import React, { useState } from "react";
import { Save } from "@material-ui/icons";
import { requestSaveHeader } from "../redux/actions/actions";
import { useDispatch, useSelector } from "react-redux";
const HeaderForm = () => {
  const { about, resumeFile } = useSelector((state) => state.user.data);
  const { header } = useSelector((state) => state.user.data.resume);
  const [headerAbout, setHeaderAbout] = useState(about);
  const [headerDesc, setHeaderDesc] = useState(header);
  const [headerResumeFile, setHeaderResumeFile] = useState(resumeFile);
  const [resumeFileName, setResumeFileName] = useState(resumeFile);
  const dispatch = useDispatch();
  const saveHeader = () => {
    dispatch(
      requestSaveHeader({ about: headerAbout, resumeFile: headerResumeFile,header:headerDesc })
    );
  };
  return (
    <>
      <Grid
        container
        xs={11}
        item
        direction="row"
        justify="space-around"
        alignItems="flex-start"
        className="my-3"
        id="port-resume"
      >
        {/* <form style={{ width: "100%" }} autoComplete="off"> */}
        <Grid
          container
          direction="row"
          justify="space-around"
          alignItems="center"
          item
          xs={12}
          md={6}
          className="border rounded py-3"
        >
          <Grid item xs={11} className="">
            <p className="h3">Header</p>
          </Grid>
          <Grid item xs={11} className="my-2">
            <FormControl className="intro-form-control">
              <textarea
                id="header-text"
                value={headerAbout}
                placeholder="Enter about section..."
                onChange={(event) => {
                  setHeaderAbout(event.target.value);
                }}
                className="border d-inline rounded"
                required
              />
            </FormControl>
          </Grid>

          <Grid item xs={11} className="my-2">
            <FormControl className="intro-form-control">
              <textarea
                id="header-desc"
                value={headerDesc}
                placeholder="Enter Header Description..."
                onChange={(event) => {
                  setHeaderDesc(event.target.value);
                }}
                className="border d-inline rounded"
                required
              />
            </FormControl>
          </Grid>

          <Grid item xs={11} className="border rounded p-2">
            <input
              className="d-none"
              id="resume-file"
              type="file"
              accept=".doc,.docx,.pdf"
              onChange={(event) => {
                setHeaderResumeFile(event.target.files[0]);
                setResumeFileName(event.target.files[0].name);
              }}
              required
            />
            <label htmlFor="resume-file">
              <Tooltip title="Upload Your Resume">
                <IconButton
                  color="primary"
                  aria-label="upload_resume"
                  component="span"
                >
                  <DescriptionIcon />
                </IconButton>
              </Tooltip>
            </label>
            {resumeFileName ? (
              <>
                <span>{resumeFileName}</span>
                <Tooltip title="Delete File" className="float-end">
                  <IconButton
                    color="primary"
                    aria-label="delete_file"
                    component="span"
                    onClick={() => {
                      setHeaderResumeFile("");
                      setResumeFileName("");
                    }}
                  >
                    <DeleteIcon />
                  </IconButton>
                </Tooltip>
              </>
            ) : (
              <>Choose your resume</>
            )}
          </Grid>
          <Grid
            item
            xs={12}
            style={{ textAlign: "right" }}
            className="px-5 my-3"
          >
            <Button
              color="primary"
              variant="contained"
              size="small"
              startIcon={<Save />}
              onClick={saveHeader}
            >
              Save
            </Button>
          </Grid>
        </Grid>
        {/* </form> */}
      </Grid>
    </>
  );
};
export default HeaderForm;
