import React, { useState } from "react";
import {
  Button,
  FormControl,
  Grid,
  IconButton,
  Input,
  InputLabel,
  Snackbar,
  Tooltip,
} from "@material-ui/core";

import { PhotoCamera } from "@material-ui/icons";
import AddCircleIcon from "@material-ui/icons/AddCircle";
import MuiAlert from "@material-ui/lab/Alert";
import DeleteIcon from "@material-ui/icons/Delete";
import ChipInput from "material-ui-chip-input";
import { useDispatch } from "react-redux";
import { requestAddProject } from "../../redux/actions/actions";
function Alert(props) {
  return <MuiAlert elevation={6} variant="filled" {...props} />;
}

const AddProjectForm = ({ projects, setProjects, show, setShow }) => {
  const [projectImage, setProjectImage] = useState("");
  const [projectImageName, setProjectImageName] = useState("");
  const [projectTitle, setProjectTitle] = useState("");
  const [projectDesc, setProjectDesc] = useState("");
  const [projectTech, setProjectTech] = useState([]);
  const [projectUrl, setProjectUrl] = useState("");
  const [openAlert, setOpenAlert] = useState(false);
  const dispatch = useDispatch();
  const addChip = (value) => {
    setProjectTech([...projectTech, value]);
  };

  const removeChip = (chip, index) => {
    const temp = projectTech.slice();
    temp.splice(index, 1);
    setProjectTech([...temp]);
  };
  const handleClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }
    setOpenAlert(false);
  };
  const addProject = () => {
    dispatch(requestAddProject(
      {
        content: projectDesc,
        image: projectImage,
        tech: projectTech,
        title: projectTitle,
        url: projectUrl,
      }
    ))
    setProjectTitle("");
    setProjectDesc("");
    setProjectTech([]);
    setProjectImage("");
    setProjectUrl("");
    setShow(!show);
  };
  return (
    <>
      <Grid item xs={11}  className="my-2">
        <FormControl className="intro-form-control" required>
          <InputLabel htmlFor="project-title">Project Title</InputLabel>
          <Input
            id="project-title"
            value={projectTitle}
            onChange={(event) => setProjectTitle(event.target.value)}
            aria-describedby="project-title-text"
          />
        </FormControl>
      </Grid>

      <Grid item xs={11} >
        <input
          accept="image/*"
          className="d-none"
          id="project-image"
          type="file"
          onChange={(event) => {
            setProjectImage(event.target.files[0]);
            setProjectImageName(event.target.files[0]["name"]);
          }}
        />
        <label htmlFor="project-image">
          <Tooltip title="Upload Project Image">
            <IconButton
              color="primary"
              aria-label="upload picture"
              component="span"
            >
              <PhotoCamera />
            </IconButton>
          </Tooltip>
        </label>
        {!projectImageName ? (
          <span>Choose Project Image</span>
        ) : (
          <>
            <span>{projectImageName}</span>
            <Tooltip title="Delete File" className="float-end">
              <IconButton
                color="primary"
                aria-label="delete_file"
                component="span"
                onClick={() => {
                  setProjectImage("");
                  setProjectImageName("");
                }}
              >
                <DeleteIcon />
              </IconButton>
            </Tooltip>
          </>
        )}
      </Grid>
      <Grid item xs={11}  className="my-2">
        <FormControl className="intro-form-control" required>
          <InputLabel htmlFor="project-desc">Project Description</InputLabel>
          <Input
            id="project-desc"
            value={projectDesc}
            onChange={(event) => setProjectDesc(event.target.value)}
            aria-describedby="project-desc-text"
          />
        </FormControl>
      </Grid>
      <Grid item xs={11} >
        <FormControl className="intro-form-control">
          <ChipInput
            id="intro-highlights"
            value={projectTech}
            onAdd={(chip) => addChip(chip)}
            onDelete={(chip, index) => removeChip(chip, index)}
            aria-describedby="intro-highlights-text"
            placeholder="Enter Heighlights"
            required
          />
        </FormControl>
      </Grid>
      <Grid item xs={11}  className="my-2">
        <FormControl className="intro-form-control" required>
          <InputLabel htmlFor="project-url">Project Url</InputLabel>
          <Input
            id="project-url"
            value={projectUrl}
            onChange={(event) => setProjectUrl(event.target.value)}
            aria-describedby="project-url-text"
          />
        </FormControl>
      </Grid>
      <Grid item xs={12} className="my-2" style={{ textAlign: "right" }}>
        <Button
          color="primary"
          variant="contained"
          size="small"
          startIcon={<AddCircleIcon />}
          onClick={
            projectTitle &&
            projectImage &&
            projectDesc &&
            projectTech &&
            projectUrl
              ? addProject
              : () => {
                  setOpenAlert(!openAlert);
                }
          }
          className="mx-5"
        >
          Add Project
        </Button>
      </Grid>
      <Snackbar open={openAlert} autoHideDuration={2000} onClose={handleClose}>
        <Alert onClose={handleClose} severity="error">
          Some fields are empty !
        </Alert>
      </Snackbar>
    </>
  );
};
export default AddProjectForm;
