import React, { useState } from "react";
import {
  Button,
  FormControl,
  Grid,
  Input,
  InputLabel,
  Select,
  Snackbar,
} from "@material-ui/core";
import MuiAlert from "@material-ui/lab/Alert";
import AddCircleIcon from "@material-ui/icons/AddCircle";
function Alert(props) {
  return <MuiAlert elevation={6} variant="filled" {...props} />;
}
const AddDataForm = ({ educationData, setEducationData, show, setShow }) => {
  const [schoolName, setSchoolName] = useState("");
  const [startingYear, setStartingYear] = useState("");
  const [endingYear, setEndingYear] = useState("");
  const [educationAbout, setEducationAbout] = useState("");
  const [openAlert, setOpenAlert] = useState(false);
  const range = (start, end) =>
    new Array(end - start + 1).fill().map((el, ind) => {
      return { year: `${ind + start}` };
    });
  const years = range(1950, 2025);
  const addEducation = () => {
    setEducationData([
      ...educationData,
      {
        name: schoolName,
        start: startingYear,
        end: endingYear,
        about: educationAbout,
      },
    ]);
    setShow(!show);
  };
  const handleClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }
    setOpenAlert(false);
  };

  return (
    <>
      <Grid item xs={12} className="my-2">
        <FormControl className="intro-form-control" required>
          <InputLabel htmlFor="education-place">School/Collage Name</InputLabel>
          <Input
            id="education-place"
            value={schoolName}
            onChange={(event) => setSchoolName(event.target.value)}
            aria-describedby="education-place-text"
          />
        </FormControl>
      </Grid>
      <Grid item xs={12} className="my-2">
        <FormControl className="intro-form-control" required>
          <InputLabel htmlFor="education-about">About Education</InputLabel>
          <Input
            id="education-about"
            value={educationAbout}
            onChange={(event) => setEducationAbout(event.target.value)}
            aria-describedby="education-place-text"
          />
        </FormControl>
      </Grid>
      <Grid item xs={6} md={3} className="my-2">
        <FormControl required className="w-100">
          <InputLabel htmlFor="resume-education-starting-year">
            Starting Year
          </InputLabel>
          <Select
            native
            onChange={(event) => {
              setStartingYear(event.target.value);
            }}
            inputProps={{
              name: "startingYear",
              id: "resume-education-starting-year",
            }}
          >
            <option aria-label="None" value=""></option>
            {years.map(({ year }) => (
              <option value={year} key={year}>
                {year}
              </option>
            ))}
          </Select>
        </FormControl>
      </Grid>
      <Grid item xs={6} md={3} className="my-2">
        <FormControl required className="w-100">
          <InputLabel htmlFor="resume-education-ending-year">
            Ending Year
          </InputLabel>
          <Select
            native
            onChange={(event) => {
              setEndingYear(event.target.value);
            }}
            inputProps={{
              name: "endingYear",
              id: "resume-education-ending-year",
            }}
          >
            <option aria-label="None" value=""></option>
            {years.map(({ year }) => (
              <option value={year} key={year}>
                {year}
              </option>
            ))}
          </Select>
        </FormControl>
      </Grid>

      <Grid item xs={12} md={5} className="mt-2" style={{ textAlign: "right" }}>
        <Button
          color="primary"
          variant="contained"
          size="small"
          startIcon={<AddCircleIcon />}
          onClick={
            educationAbout && schoolName && startingYear && endingYear
              ? addEducation
              : () => {
                  setOpenAlert(!openAlert);
                }
          }
        >
          Add Education
        </Button>
      </Grid>
      <Snackbar open={openAlert} autoHideDuration={2000} onClose={handleClose}>
        <Alert onClose={handleClose} severity="error">
          Some fields are empty !
        </Alert>
      </Snackbar>
    </>
  );
};
export default AddDataForm;
