import React, { useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import FormControl from "@material-ui/core/FormControl";
import InputLabel from "@material-ui/core/InputLabel";

import ChipInput from "material-ui-chip-input";
import {
  Button,
  Grid,
  IconButton,
  OutlinedInput,
  Tooltip,
} from "@material-ui/core";
import SaveIcon from "@material-ui/icons/Save";
import DeleteIcon from "@material-ui/icons/Delete";
import { PhotoCamera } from "@material-ui/icons";
import { useDispatch, useSelector } from "react-redux";
import { requestSaveIntroduction } from "../redux/actions/actions";
const useStyles = makeStyles((theme) => ({}));

export default function IntrodunctionForm() {
  const {
    name,
    email,
    age,
    phone,
    address,
    socialLinks,
    passportPhoto,
    highlights,
  } = useSelector((state) => state.user.data);
  const dispatch = useDispatch();
  const [introName, setIntroName] = useState(name);
  const [introEmail, setIntroEmail] = useState(email);
  const [introAge, setIntroAge] = useState(age);
  const [introAddress, setIntroAddress] = useState(address);
  const [introMobile, setIntroMobile] = useState(phone);
  const [introGithubUrl, setIntroGithubUrl] = useState(socialLinks.github);
  const [introLinkedInUrl, setIntroLinkedInUrl] = useState(
    socialLinks.linkedin
  );
  const [introInstagramUrl, setIntroInstagramUrl] = useState(
    socialLinks.instagram
  );
  const [introHighlights, setIntroHighlights] = useState([...highlights]);
  const [introImage, setIntroImage] = useState(passportPhoto);
  const [introImageName, setIntroImageName] = useState(passportPhoto);
  const addChip = (value) => {
    setIntroHighlights([...introHighlights, value]);
  };

  const removeChip = (chip, index) => {
    const temp = introHighlights.slice();
    temp.splice(index, 1);
    setIntroHighlights([...temp]);
  };
  const saveIntroduction = () => {
    dispatch(
      requestSaveIntroduction({
        name: introName,
        email: introEmail,
        age: introAge,
        phone: introMobile,
        address: introAddress,
        socialLinks: {
          github: introGithubUrl,
          linkedin: introLinkedInUrl,
          instagram: introInstagramUrl,
        },
        photo: introImage,
        highlights: introHighlights,
      })
    );
  };
  return (
    <>
      <Grid container item xs={11} md={6} id="port-intro" className="mx-auto">
        <form autoComplete="off">
          <Grid
            container
            direction="row"
            justify="space-around"
            alignItems="flex-start"
            item
            xs={12}
          >
            <Grid
              container
              item
              direction="row"
              justify="space-around"
              alignItems="center"
              className="border rounded py-3 my-3"
              xs={11}
            >
              <Grid item xs={11} className="p-3">
                <p className="h3">Introduction</p>
              </Grid>
              <Grid item xs={11} className="my-2">
                <FormControl
                  className="intro-form-control"
                  variant="outlined"
                  variant="outlined"
                >
                  <OutlinedInput
                    id="intro-name"
                    placeholder="Name"
                    value={introName}
                    onChange={(event) => setIntroName(event.target.value)}
                    label="Name"
                    required
                  />
                </FormControl>
              </Grid>
              <Grid item xs={11} className="my-2">
                <FormControl
                  className="intro-form-control"
                  variant="outlined"
                  variant="outlined"
                >
                  {/* <InputLabel htmlFor="intro-email">Email</InputLabel> */}
                  <OutlinedInput
                    id="intro-email"
                    type="email"
                    placeholder="Email"
                    value={introEmail}
                    onChange={(event) => setIntroEmail(event.target.value)}
                    aria-describedby="intro-email-text"
                    required
                  />
                </FormControl>
              </Grid>
              <Grid item xs={11} className="my-2">
                <FormControl className="intro-form-control" variant="outlined">
                  {/* <InputLabel htmlFor="intro-age">Age</InputLabel> */}
                  <OutlinedInput
                    id="intro-age"
                    value={introAge}
                    onChange={(event) => setIntroAge(event.target.value)}
                    aria-describedby="intro-age-text"
                    required
                  />
                </FormControl>
              </Grid>
              <Grid item xs={11} className="my-2">
                <FormControl className="intro-form-control" variant="outlined">
                  {/* <InputLabel htmlFor="intro-mobile">Mobile</InputLabel> */}
                  <OutlinedInput
                    id="intro-mobile"
                    value={introMobile}
                    placeholder="Contact No."
                    onChange={(event) => setIntroMobile(event.target.value)}
                    aria-describedby="intro-mobile-text"
                    required
                  />
                </FormControl>
              </Grid>
              <Grid item xs={11} className="my-2">
                <FormControl className="intro-form-control" variant="outlined">
                  {/* <InputLabel htmlFor="intro-address">Address</InputLabel> */}
                  <OutlinedInput
                    id="intro-address"
                    value={introAddress}
                    onChange={(event) => setIntroAddress(event.target.value)}
                    aria-describedby="intro-address-text"
                    placeholder="Current Address"
                    required
                  />
                </FormControl>
              </Grid>
              <Grid item xs={11} className="my-2">
                <FormControl className="intro-form-control" variant="outlined">
                  {/* <InputLabel htmlFor="intro-github-url">GithubUrl</InputLabel> */}
                  <OutlinedInput
                    id="intro-github-url"
                    value={introGithubUrl}
                    placeholder="Github Profile Url"
                    onChange={(event) => setIntroGithubUrl(event.target.value)}
                    aria-describedby="intro-github-url-text"
                    required
                  />
                </FormControl>
              </Grid>
              <Grid item xs={11} className="my-2">
                <FormControl className="intro-form-control" variant="outlined">
                  {/* <InputLabel htmlFor="intro-linkedin-url">
                    LinkedIn Url
                  </InputLabel> */}
                  <OutlinedInput
                    id="intro-linkedin-url"
                    value={introLinkedInUrl}
                    placeholder="Linked in profile Url"
                    onChange={(event) =>
                      setIntroLinkedInUrl(event.target.value)
                    }
                    aria-describedby="intro-linkedin-url-text"
                    required
                  />
                </FormControl>
              </Grid>
              <Grid item xs={11} className="my-2">
                <FormControl className="intro-form-control" variant="outlined">
                  {/* <InputLabel htmlFor="intro-instagram-url">
                    Instagram Url
                  </InputLabel> */}
                  <OutlinedInput
                    id="intro-instagram-url"
                    placeholder="Instagram profile Url"
                    value={introInstagramUrl}
                    onChange={(event) =>
                      setIntroInstagramUrl(event.target.value)
                    }
                    aria-describedby="intro-instagram-url-text"
                    required
                  />
                </FormControl>
              </Grid>
              <Grid item xs={11} className="my-2">
                <FormControl className="intro-form-control" variant="outlined">
                  <ChipInput
                    id="intro-highlights"
                    value={introHighlights}
                    onAdd={(chip) => addChip(chip)}
                    onDelete={(chip, index) => removeChip(chip, index)}
                    aria-describedby="intro-highlights-text"
                    placeholder="Enter Heighlights"
                    variant="outlined"
                    required
                  />
                </FormControl>
              </Grid>
              <Grid
                item
                container
                direction="row"
                justify="space-between"
                alignItems="center"
                xs={11}
              >
                <Grid item container xs={9} direction="row" alignItems="center">
                  <input
                    accept="image/*"
                    className="d-none"
                    id="icon-button-file"
                    type="file"
                    onChange={(event) => {
                      setIntroImage(event.target.files[0]);
                      setIntroImageName(event.target.files[0]["name"]);
                    }}
                  />
                  <label htmlFor="icon-button-file">
                    <Tooltip title="Upload Your Image">
                      <IconButton
                        color="primary"
                        aria-label="upload picture"
                        component="span"
                      >
                        <PhotoCamera />
                      </IconButton>
                    </Tooltip>
                  </label>
                  {!introImageName ? (
                    <Grid item>Choose Project Image</Grid>
                  ) : (
                    <>
                      <Grid item>
                        <span
                          style={{ maxWidth: "300px" }}
                          className="d-inline-block text-truncate"
                        >
                          {introImageName}
                        </span>
                      </Grid>
                      <Grid item>
                        <Tooltip title="Delete File" className="float-end">
                          <IconButton
                            color="primary"
                            aria-label="delete_file"
                            component="span"
                            onClick={() => {
                              setIntroImage("");
                              setIntroImageName("");
                            }}
                          >
                            <DeleteIcon />
                          </IconButton>
                        </Tooltip>
                      </Grid>
                    </>
                  )}
                </Grid>
                <Grid item className="mx-3">
                  <Button
                    color="primary"
                    variant="contained"
                    size="small"
                    startIcon={<SaveIcon />}
                    onClick={saveIntroduction}
                  >
                    Save
                  </Button>
                </Grid>
              </Grid>
            </Grid>
          </Grid>
        </form>
      </Grid>
    </>
  );
}
