import React, { useEffect, useState } from "react";
import Button from "@material-ui/core/Button";
import TextField from "@material-ui/core/TextField";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogTitle from "@material-ui/core/DialogTitle";
import { useDispatch, useSelector } from "react-redux";
import { requestDeploy } from "../redux/actions/actions";
import { Tooltip } from "@material-ui/core";

export default function DeployModal({ open, setOpen }) {
  const { giturl, deploymentStatus } = useSelector((state) => state.user);
  const username = useSelector((state) => state.user.data.name);
  const dispatch = useDispatch();
  const [git, setGit] = useState(giturl);
  const handleClickOpen = () => {
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);
  };
  const handleSubmit = (event) => {
    event.preventDefault();
    dispatch(requestDeploy({giturl:git,name:username}));
    handleClose();
  };
  useEffect(() => {
    setGit(giturl);
  }, [giturl]);
  return (
    <div>
      <Dialog
        open={open}
        onClose={handleClose}
        aria-labelledby="form-dialog-title"
      >
        <form onSubmit={handleSubmit}>
          <DialogTitle id="form-dialog-title">
            Request to deploy your portfolio{" "}
          </DialogTitle>
          <DialogContent>
            <DialogContentText>
              Let's deploy this portfolio on github using github pages, enter
              url of your public github repository.
            </DialogContentText>
            <TextField
              autoFocus
              margin="dense"
              id="giturl"
              value={git}
              onChange={(event) => setGit(event.target.value)}
              label="Github repository URL"
              type="text"
              disabled={deploymentStatus == 1 ? true : false}
              fullWidth
              required
            />
          </DialogContent>
          <DialogActions>
            <Button
              onClick={handleClose}
              style={{
                backgroundColor: "#d9534f",
                borderColor: "#d43f3a",
                color: "white",
              }}
            >
              Cancel
            </Button>
            {deploymentStatus > 0 ? (
              <Tooltip style={{zIndex:"10002"}} title="Your Portfolio is already deployed, if you want to change git repository contact to customer support">
                <Button
                  className="bg-primary text-white"
                >
                  Already Deployed
                </Button>
              </Tooltip>
            ) : (
              <Button type="submit" className="bg-primary text-white">
                Make Request
              </Button>
            )}
          </DialogActions>
        </form>
      </Dialog>
    </div>
  );
}
