import React, { useState } from "react";
import AddCircleIcon from "@material-ui/icons/AddCircle";
import MuiAlert from "@material-ui/lab/Alert";

import {
  Button,
  FormControl,
  Grid,
  Input,
  InputLabel,
  Snackbar,
} from "@material-ui/core";
function Alert(props) {
  return <MuiAlert elevation={6} variant="filled" {...props} />;
}

const AddContactLink = ({ contactLinks, setContactLinks }) => {
  const [websiteName, setWebsiteName] = useState("");
  const [profileUrl, setProfileUrl] = useState("");
  const [openAlert, setOpenAlert] = useState(false);

  const addProfiles = () => {
    setContactLinks([...contactLinks, { name: websiteName, url: profileUrl }]);
    setWebsiteName("");
    setProfileUrl("");
  };
  const handleClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }
    setOpenAlert(false);
  };

  return (
    <>
      <Grid container item xs={12}>
        <Grid item xs={9}>
          <FormControl className="intro-form-control" required>
            <InputLabel htmlFor="website-name">Website Name</InputLabel>
            <Input
              id="website-name"
              value={websiteName}
              onChange={(event) => setWebsiteName(event.target.value)}
              aria-describedby="skill-name-text"
            />
          </FormControl>
        </Grid>
        <Grid item xs={8}>
          <FormControl required className="w-100">
            <InputLabel htmlFor="profile-url">Profile Url</InputLabel>
            <Input
              id="profile-url"
              value={profileUrl}
              onChange={(event) =>
                 setProfileUrl(event.target.value)
                }
              aria-describedby="profile-url-text"
            />

          </FormControl>
        </Grid>
        <Grid item xs={4} className="mt-2" style={{ textAlign: "right" }}>
          <Button
            color="primary"
            variant="contained"
            size="small"
            startIcon={<AddCircleIcon />}
            onClick={
              profileUrl && websiteName ?addProfiles:() => {
                setOpenAlert(!openAlert);
              }}
          >
            Add Profile
          </Button>
        </Grid>
      </Grid>
      <Snackbar open={openAlert} autoHideDuration={2000} onClose={handleClose}>
        <Alert onClose={handleClose} severity="error">
          Some fields are empty !
        </Alert>
      </Snackbar>
    </>
  );
};

export default AddContactLink;
