import { Dialog, Typography, withStyles } from "@material-ui/core";
import MuiDialogContent from "@material-ui/core/DialogContent";

import ReportProblemRoundedIcon from "@material-ui/icons/ReportProblemRounded";
const DialogContent = withStyles((theme) => ({
  root: {
    padding: theme.spacing(2),
  },
}))(MuiDialogContent);

const NotVerified = () => {
  return (
    <Dialog
      aria-labelledby="customized-dialog-title"
      open={true}
      className="rounded"
    >
      <DialogContent className="text-center" dividers>
        <div>
          <ReportProblemRoundedIcon
            style={{ fontSize: 70 }}
            className="text-danger"
          />
        </div>
        <div className="h3 my-2">Activate your account</div>
        <Typography gutterBottom>
          Click on link sent on your mail to activate your account
        </Typography>
      </DialogContent>
    </Dialog>
  );
};

export default NotVerified;
