import React from 'react'

import { Button, Grid, IconButton, makeStyles, Tooltip } from "@material-ui/core";
import CancelIcon from '@material-ui/icons/Cancel';
import { useDispatch } from 'react-redux';
import { requestDeleteProject } from '../redux/actions/actions';
import { BACK_END_URL } from '../config';
const useStyles = makeStyles((theme) => ({
  shadow: {
    boxShadow: " 0 1px 4px rgb(0 0 0 / 14%)",
  },
}));

const ShowProject = ({ image, title, content, tech, url,_id }) => {
    const classes = useStyles();
    const dispatch = useDispatch()
    const deleteProject = (id)=>{
      dispatch(requestDeleteProject(id))
    } 
    return (
      <>
        <Grid container item xs={12} className="border rounded project-div my-3">
          <Grid item sm={5} md={4} xs={12}>
            <img
              className="img-fluid project-image"
              alt="project_image"
              src={`${BACK_END_URL}/${image}`}
              style={{ height: "100%", width: "100%" }}
            />
          </Grid>
          <Grid item sm={6} md={8} xs={12} className="p-3 project-section">
            <div className="row">
            <div className="col-6 text-uppercase project-title my-3 mr-auto">{title}</div>
            <div className="col-6 ml-auto" style={{textAlign:"right"}}>
              <Tooltip title="Delete Project">
              <IconButton size="medium" onClick={()=>{deleteProject(_id)}}><CancelIcon/></IconButton>
              </Tooltip>
              </div>
            </div>
            <div className="project-content">{content}</div>
            <div>
              <div className="text-uppercase mt-3 project-stack">
                USED stacks:
              </div>
              <div className="my-3">
                {tech ? (
                  tech.map((technology, index) => (
                    <Button
                      key={index}
                      className={`${classes.shadow} mx-2 my-2 project-tech`}
                      size="small"
                    >
                      {technology}
                    </Button>
                  ))
                ) : (
                  <></>
                )}
              </div>
            </div>
            <div>
              <a
                href={url}
                style={{ maxWidth: "80%" }}
                target="_blank"
                rel="noreferrer" 
                className="project-urd-inline-block text-truncate"
              >
                {url}
              </a>
            </div>
          </Grid>
        </Grid>
      </>
    );
  };
  export default ShowProject;