import React from "react";

import { List, ListItem, ListItemIcon, ListItemText } from "@material-ui/core";
import AssignmentIndIcon from "@material-ui/icons/AssignmentInd";
import ContactMailIcon from "@material-ui/icons/ContactMail";
import RecentActorsIcon from "@material-ui/icons/RecentActors";
import BusinessCenterIcon from "@material-ui/icons/BusinessCenter";
import NoteAddIcon from "@material-ui/icons/NoteAdd";
import ArtTrackIcon from "@material-ui/icons/ArtTrack";
import SchoolIcon from "@material-ui/icons/School";
import CodeIcon from "@material-ui/icons/Code";
import { HashLink, NavHashLink } from "react-router-hash-link";
const LeftNav = () => {
  const navigation = [
    {
      text: "Introduction",
      icon: <AssignmentIndIcon />,
      href: "#port-intro",
    },
    {
      text: "Header",
      icon: <NoteAddIcon />,
      href: "#port-resume",
    },
    {
      text: "Education",
      icon: <SchoolIcon />,
      href: "#port-education",
    },
    {
      text: "Employment",
      icon: <BusinessCenterIcon />,
      href: "#port-employment",
    },
    {
      text: "General Skills",
      icon: <CodeIcon />,
      href: "#port-skills",
    },
    {
      text: "Projects",
      icon: <ArtTrackIcon />,
      href: "#port-project",
    },
    {
      text: "Contact Links",
      icon: <ContactMailIcon />,
      href: "#port-link",
    },
    {
      text: "Contact Details",
      icon: <RecentActorsIcon />,
      href: "#port-id",
    },
  ];
  return (
    <>
      <div className="position-fixed" style={{ top: "20px", left: "50px" }}>
        <List id="port-leftnav">
          {navigation.map(({ text, icon, href }, index) => (
            <NavHashLink className="nav-link" activeClassName="nav-active" to={href} key={text}>
              <ListItem button key={text}>
                <ListItemIcon>{icon}</ListItemIcon>
                <ListItemText primary={text} />
              </ListItem>
            </NavHashLink>
          ))}
        </List>
      </div>
    </>
  );
};
export default LeftNav;
