import axios from "axios";
import FormData from "form-data";
import { BACK_END_URL } from "../../config";
const host_url = BACK_END_URL;
// const host_url = "http://localhost:5000";
const fetchUserDetails = () => {
  const userEmail = localStorage.getItem("email");
  const data = axios
    .get(`${host_url}/api/resumeData?userEmail=${userEmail}`)
    .then(({ data }) => {
      return data;
    })
    .catch((error) => {
      throw new Error(error);
    });
  return data;
};
const fetchTemp = () => {
  const { data } = axios
    .get(`${host_url}/api/projects`)
    .then(({ data }) => {
      let project = [];
      data.map(({ content, img, tech, url, title }) => {
        project.push({
          content,
          title,
          tech,
          url,
          img: "data:" + img.mimetype + ";base64," + btoa(img.data),
        });
      });
      return project;
    })
    .catch((error) => {
      throw new Error(error);
    });
  return data;
};
const deleteProject = async (id) => {
  try {
    const userEmail = localStorage.getItem("email");
    let { data } = await axios.delete(`${host_url}/api/projects`, {
      data: { id, userEmail },
    });
    return data;
  } catch (err) {
    throw err;
  }
};

const saveProjects = async (projects) => {
  const userEmail = localStorage.getItem("email");
  const formData = new FormData();
  var projectData = [];
  projects.map(({ image, content, tech, title, url }) => {
    formData.set(image.name, image);
    projectData.push({
      content,
      tech,
      title,
      url,
    });
  });
  formData.set("projects", JSON.stringify(projectData));
  formData.set("userEmail", userEmail);
  let res = await axios.put(`${host_url}/api/projects`, formData, {
    headers: { "Content-Type": "multipart/form-data" },
  });
  return res;
};

const updateIntroduction = async (introDetails) => {
  const userEmail = localStorage.getItem("email");
  const formData = new FormData();
  formData.set("name", introDetails.name);
  formData.set("email", introDetails.email);
  formData.set("age", introDetails.age);
  formData.set("phone", introDetails.phone);
  formData.set("address", introDetails.address);
  formData.set("socialLinks", JSON.stringify(introDetails.socialLinks));
  formData.set("highlights", JSON.stringify(introDetails.highlights));
  if(typeof introDetails.photo==typeof "ab"){
    formData.set("passportPhoto", introDetails.photo);
  }else{
    formData.set("photo", introDetails.photo);
  }
  formData.set("userEmail", userEmail);
  let { data } = await axios.put(`${host_url}/api/intro`, formData, {
    headers: { "Content-Type": "multipart/form-data" },
  });
  return data;
};

const updateHeader = async (headDetails) => {
  const userEmail = localStorage.getItem("email");
  const formData = new FormData();
  formData.set("about", headDetails.about);
  formData.set("header", headDetails.header);
  formData.set("resumeFile", headDetails.resumeFile);
  formData.set("userEmail", userEmail);
  let { data } = await axios.put(`${host_url}/api/header`, formData, {
    headers: { "Content-Type": "multipart/form-data" },
  });
  return data;
};
const saveEducation = async (educationDetails) => {
  const userEmail = localStorage.getItem("email");
  let { data } = await axios.put(`${host_url}/api/resume/education`, {
    educationDetails,
    userEmail,
  });
  return data;
};
const saveEmployment = async (employmentDetails) => {
  const userEmail = localStorage.getItem("email");
  let { data } = await axios.put(`${host_url}/api/resume/employment`, {
    employmentDetails,
    userEmail,
  });
  return data;
};
const saveSkills = async (generalSkills) => {
  const userEmail = localStorage.getItem("email");
  let { data } = await axios.put(`${host_url}/api/resume/skills`, {
    generalSkills,
    userEmail,
  });
  return data;
};

const saveProfiles = async (contactLinks) => {
  const userEmail = localStorage.getItem("email");
  const profileData = contactLinks.map(({ name, url }) => {
    return { name, url };
  });
  let { data } = await axios.put(`${host_url}/api/contactLinks`, {
    profileData,
    userEmail,
  });
  return data;
};
const saveContact = async (contacts) => {
  const userEmail = localStorage.getItem("email");
  const profileData = contacts.map(({ name, id }) => {
    return { name, id };
  });
  let { data } = await axios.put(`${host_url}/api/contacts`, {
    profileData,
    userEmail,
  });
  return data;
};

const addProject = async (project) => {
  const userEmail = localStorage.getItem("email");
  const formData = new FormData();
  formData.set("title", project.title);
  formData.set("content", project.content);
  formData.set("tech", JSON.stringify(project.tech));
  formData.set("url", project.url);
  formData.set("image", project.image);
  formData.set("userEmail", userEmail);
  let { data } = await axios.post(`${host_url}/api/projects`, formData, {
    headers: { "Content-Type": "multipart/form-data" },
  });
  return data;
};

const checkUser = async ({ username, password }) => {
  try {
    let { data } = await axios.post(`${host_url}/api/authLogin`, {
      username,
      password,
    });
    if (data.email) {
      localStorage.setItem("token", data.token);
      localStorage.setItem("email", data.email);
      localStorage.setItem("auth", data.id);
      return { email: data.email, status: data.status, expiry: data.expiry,auth:data.id };
    } else {
      throw new Error(data.message);
    }
  } catch (err) {
    throw err;
  }
};
const verifyUser = async ({ token, email }) => {
  try {
    let { data } = await axios.post(`${host_url}/api/isVerify`, {
      token,
      email,
    });
    if (data.email) {
      return data;
    } else {
      throw new Error(data.message);
    }
  } catch (err) {
    throw err;
  }
};
const createProfile = async ({ email, username, password }) => {
  try {
    let { data } = await axios.post(`${host_url}/api/resumeData`, {
      email,
      username,
      password,
    });
    if (data.error) {
      throw new Error(data.error);
    } else {
      localStorage.setItem("token", data.token);
      localStorage.setItem("email", data.email);
      return data;
    }
  } catch (err) {
    throw err;
  }
};
const deployPortfolio = async ({giturl,name}) => {
  const userEmail = localStorage.getItem("email");
  try{
    let { data } = await axios.post(`${host_url}/api/deploy`, {
      giturl,
      name,
      userEmail
    });
    return data;
  }catch(err){
    throw err;
  }
};

const gitUrl = async () => {
  const userEmail = localStorage.getItem("email");
  try{
    let { data } = await axios.get(`${host_url}/api/deploy?email=${userEmail}`, {});
    return data;
  }catch(err){
    throw err;
  }
};


export {
  fetchUserDetails,
  saveProjects,
  fetchTemp,
  addProject,
  updateIntroduction,
  updateHeader,
  saveEducation,
  saveEmployment,
  saveSkills,
  saveProfiles,
  saveContact,
  checkUser,
  verifyUser,
  deleteProject,
  createProfile,
  deployPortfolio,
  gitUrl
};
