import React, { useState } from "react";
import {
  Button,
  Divider,
  Grid,
  IconButton,
  Snackbar,
  Tooltip,
} from "@material-ui/core";
import DeleteIcon from "@material-ui/icons/Delete";
import ArrowUpwardIcon from "@material-ui/icons/ArrowUpward";
import ArrowDownwardIcon from "@material-ui/icons/ArrowDownward";
import AddIcon from "@material-ui/icons/Add";
import { Save } from "@material-ui/icons";
import Fab from "@material-ui/core/Fab";
import AddEmploymentForm from "./AddEmploymentForm";
import MuiAlert from "@material-ui/lab/Alert";
import { useDispatch, useSelector } from "react-redux";
import { requestSaveEmployment } from "../../redux/actions/actions";

function Alert(props) {
  return <MuiAlert elevation={6} variant="filled" {...props} />;
}

const EmploymentForm = () => {
  const { employment } = useSelector((state) => state.user.data.resume);
  const dispatch = useDispatch();
  const [showMoveUp, setShowMoveUp] = useState(false);
  const [showMoveDown, setShowMoveDown] = useState(false);
  const [employmentData, setEmploymentData] = useState(employment);
  const [showEmploymentForm, setShowEmploymentForm] = useState(false);
  const moveUp = (from, to) => {
    if (to < 0) {
      setShowMoveUp(true);
    } else {
      let temp = [...employmentData];
      temp[from] = employmentData[to];
      temp[to] = employmentData[from];
      setEmploymentData([...temp]);
    }
  };
  const moveDown = (from, to) => {
    if (to == employmentData.length) {
      setShowMoveDown(true);
    } else {
      let temp = [...employmentData];
      temp[from] = employmentData[to];
      temp[to] = employmentData[from];
      setEmploymentData([...temp]);
    }
  };
  const removeEmployment = (index) => {
    let temp = [...employmentData];
    temp.splice(index, 1);
    setEmploymentData(temp);
  };
  const handleUpClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }
    setShowMoveUp(false);
  };
  const handleDownClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }
    setShowMoveDown(false);
  };
  const saveEmployment = () => {
    dispatch(requestSaveEmployment(employmentData));
  };
  return (
    <>
      <Grid
        container
        direction="row"
        justify="space-around"
        alignItems="flex-start"
        item
        xs={11}
        id="port-employment"
        className="mx-auto"
      >
        <Grid item xs={11} md={6} className="p-3 my-3 border rounded ">
          <Grid item xs={12} className="h3 m-3">
            Employment
          </Grid>
          <Grid item xs={12} className="">
            {employmentData.length ? (
              <table className="table small table-small text-center">
                <thead>
                  <tr>
                    <td style={{ textAlign: "left" }}>Company Name</td>
                    <td style={{ minWidth: "80px" }}>Start-End</td>
                    <td>About Employment</td>
                    <td>Remove</td>
                    <td>Shift</td>
                  </tr>
                </thead>
                <tbody style={{ verticalAlign: "middle" }}>
                  {employmentData.map(({ name, start, end, about }, index) => (
                    <tr key={index}>
                      <td style={{ textAlign: "left" }}>{name}</td>
                      <td>
                        <div>{start}</div>
                        <div> to </div>
                        <div>{end}</div>
                      </td>
                      <td>{about}</td>
                      <td>
                        <Tooltip title="Delete Employment">
                          <IconButton
                            className="bg-danger text-white fw-bold"
                            size="small"
                            onClick={() => {
                              removeEmployment(index);
                            }}
                          >
                            <DeleteIcon />
                          </IconButton>
                        </Tooltip>
                      </td>
                      <td>
                        <IconButton
                          size="small"
                          onClick={() => {
                            moveUp(index, index - 1);
                          }}
                        >
                          <ArrowUpwardIcon />
                        </IconButton>
                        <IconButton
                          size="small"
                          onClick={() => {
                            moveDown(index, index + 1);
                          }}
                        >
                          <ArrowDownwardIcon />
                        </IconButton>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            ) : (
              <>
                <p className="text-center">There are no employments add some</p>
                <Divider className="bg-dark" />
              </>
            )}
          </Grid>
          <Grid
            container
            item
            direction="row"
            justify="space-between"
            alignItems="center"
            xs={12}
          >
            {showEmploymentForm ? (
              <AddEmploymentForm
                employmentData={employmentData}
                setEmploymentData={setEmploymentData}
                setShow={setShowEmploymentForm}
                show={showEmploymentForm}
              />
            ) : (
              <Grid
                container
                direction="row"
                justify="space-between"
                alignItems="center"
                item
                xs={12}
                className="px-3"
              >
                <Grid item className="mr-auto">
                  <span>
                    Add {employmentData.length ? "more" : ""} employment
                  </span>
                </Grid>
                <Grid item className="ml-auto">
                  <Tooltip title="Add Employment">
                    <Fab
                      size="small"
                      color="primary"
                      aria-label="add"
                      onClick={() => {
                        setShowEmploymentForm(!showEmploymentForm);
                      }}
                    >
                      <AddIcon />
                    </Fab>
                  </Tooltip>
                </Grid>
              </Grid>
            )}
            {employmentData.length ? (
              <Grid
                item
                xs={12}
                style={{ textAlign: "right" }}
                className="px-3 mt-4"
              >
                <Button
                  color="primary"
                  variant="contained"
                  size="small"
                  startIcon={<Save />}
                  onClick={saveEmployment}
                >
                  Save
                </Button>
              </Grid>
            ) : (
              <></>
            )}
          </Grid>
          <Snackbar
            open={showMoveUp}
            autoHideDuration={2000}
            onClose={handleUpClose}
          >
            <Alert onClose={handleUpClose} severity="warning">
              This is already on top !
            </Alert>
          </Snackbar>
          <Snackbar
            open={showMoveDown}
            autoHideDuration={2000}
            onClose={handleDownClose}
          >
            <Alert onClose={handleDownClose} severity="warning">
              This is already on bottom !
            </Alert>
          </Snackbar>
        </Grid>
      </Grid>
    </>
  );
};
export default EmploymentForm;
