import React, { useState } from "react";
import { Button, Divider, Grid } from "@material-ui/core";
import DeleteIcon from "@material-ui/icons/Delete";
import AddContactLink from "./AddContactLink";
import { Save } from "@material-ui/icons";
import { useDispatch, useSelector } from "react-redux";
import {  requestSaveProfiles } from "../../redux/actions/actions";

const ContactLinkForm = () => {
  const  data  = useSelector((state) => state.user.data.contactLinks);
  const dispatch = useDispatch();
  const [contactLinks, setContactLinks] = useState(data);
  const removeContactLink = (index) => {
    let temp = [...contactLinks];
    temp.splice(index, 1);
    setContactLinks([...temp]);
  };
  const saveContactLinks = () => {
    dispatch(requestSaveProfiles(contactLinks));
  };
  return (
    <>
    <Grid container item xs={11} className="my-3" id="port-link">
      <Grid
        container
        item
        xs={11}
        md={6}
        className='mx-auto border rounded p-3'
        >
        <Grid item xs={12} className="h2 m-3">
          Contact Links
        </Grid>
        <Grid item xs={12} >
          {contactLinks.length ? (
            <table className="table table-sm small vertical">
              <thead>
                <tr>
                  <th>Website</th>
                  <th style={{ textAlign: "center" }}>Profile Url</th>
                  <th style={{ textAlign: "right" }}>Remove Link</th>
                </tr>
              </thead>
              <tbody style={{verticalAlign:"middle"}}>
                {contactLinks.map(({ name, url }, index) => (
                  <tr key={index}>
                    <td>{name}</td>
                    <td style={{ textAlign: "center" }}><a href={url}>{url}</a></td>
                    <td style={{ textAlign: "right" }}>
                      {" "}
                      <Button
                        className="bg-danger text-white fw-bold"
                        size="small"
                        onClick={() => {
                          removeContactLink(index);
                        }}
                        startIcon={<DeleteIcon />}
                        >
                        Delete
                      </Button>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          ) : (
            <>
            <p className="text-center mt-3">Add Some Social Media Profiles to get better touch.</p>
            <Divider className="bg-dark mx-auto" style={{width:'90%'}} />
            </>
            )}
        </Grid>
        <Grid
          container
          item
          direction="column"
          justify="flex-end"
          alignItems="flex-end"
          xs={12}
          >
          <AddContactLink
           contactLinks={contactLinks} setContactLinks={setContactLinks} />
          {contactLinks.length ? (
            <Grid
            item
            xs={12}
            style={{ textAlign: "right" }}
            className="mt-4"
            >
              <Button
                color="primary"
                variant="contained"
                size="small"
                startIcon={<Save />}
                className="mx-3"
                onClick={saveContactLinks}
                >
                Save
              </Button>
            </Grid>
          ) : (
            <></>
            )}
        </Grid>
      </Grid>
            </Grid>
    </>
  );
};
export default ContactLinkForm;
