import React, { useState } from "react";
import {
  Button,
  Divider,
  Grid,
  IconButton,
  Snackbar,
  Tooltip,
} from "@material-ui/core";
import DeleteIcon from "@material-ui/icons/Delete";
import ArrowUpwardIcon from "@material-ui/icons/ArrowUpward";
import ArrowDownwardIcon from "@material-ui/icons/ArrowDownward";
import AddIcon from "@material-ui/icons/Add";
import { Save} from "@material-ui/icons";
import Fab from "@material-ui/core/Fab";
import AddDataForm from "./AddDataForm";
import MuiAlert from "@material-ui/lab/Alert";
import { useDispatch, useSelector } from "react-redux";
import { requestSaveEducation } from "../../redux/actions/actions";
function Alert(props) {
  return <MuiAlert elevation={6} variant="filled" {...props} />;
}

const EducationForm = () => {
  const {education} = useSelector(state => state.user.data.resume);
  const dispatch = useDispatch();
  const [showMoveUp, setShowMoveUp] = useState(false);
  const [showMoveDown, setShowMoveDown] = useState(false);
  const [educationData, setEducationData] = useState(education);
  const [showEducationForm, setShowEducationForm] = useState(false);
  const moveUp = (from, to) => {
    if (to < 0) {
      setShowMoveUp(!showMoveUp);
    } else {
      let temp = [...educationData];
      temp[from] = educationData[to];
      temp[to] = educationData[from];
      setEducationData([...temp]);
    }
  };
  const moveDown = (from, to) => {
    if (to == educationData.length) {
      setShowMoveDown(!showMoveDown);
    } else {
      let temp = [...educationData];
      temp[from] = educationData[to];
      temp[to] = educationData[from];
      setEducationData([...temp]);
    }
  };
  const removeEducation = (index) => {
    let temp = [...educationData];
    temp.splice(index, 1);
    setEducationData(temp);
  };
  const handleUpClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }
    setShowMoveUp(false);
  };
  const handleDownClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }
    setShowMoveDown(false);
  };
  const saveEducation = ()=>{
    dispatch(requestSaveEducation(educationData))  
  }
  return (
    <>
      <Grid
        container
        direction="row"
        justify="space-around"
        alignItems="flex-start"
        item
        xs={11}
        id="port-education"
        className="mx-auto"
      >
        <Grid item xs={11} md={6} className="p-3 my-3 border rounded ">
        <Grid item xs={12} className="h3 m-3">
            Education
          </Grid>

        <Grid item xs={12} className="">
          {educationData.length ? (
            <table className="table small table-small text-center">
              <thead>
                <tr>
                  <td style={{textAlign:"left"}}>School Name</td>
                  <td>Year</td>
                  <td>About Education</td>
                  <td>Delete</td>
                  <td>Shift</td>
                </tr>
              </thead>
              <tbody  style={{verticalAlign:"middle"}}>
                {educationData.map(({ name, start, end, about }, index) => (
                  <tr key={index}>
                    <td style={{textAlign:"left"}}>{name}</td>
                    <td>
                      {start} to {end}
                    </td>
                    <td>{about}</td>
                    <td>
                      <IconButton
                        className="bg-danger text-white fw-bold"
                        size="small"
                        onClick={() => {
                          removeEducation(index);
                        }}

                      >
                      <DeleteIcon />
                      </IconButton>
                    </td>
                    <td>
                      <IconButton
                        size="small"
                        onClick={() => {
                          moveUp(index, index - 1);
                        }}
                      >
                        <ArrowUpwardIcon />
                      </IconButton>
                      <IconButton
                        size="small"
                        onClick={() => {
                          moveDown(index, index + 1);
                        }}
                      >
                        <ArrowDownwardIcon />
                      </IconButton>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          ) : (
            <>
              <p className="text-center">There are no educations add some</p>
              <Divider className="bg-dark" />
            </>
          )}
        </Grid>
        <Grid
          container
          item
          direction="row"
          justify="space-between"
          alignItems="center"
          xs={12}
        >
          {showEducationForm ? (
            <AddDataForm
              educationData={educationData}
              setEducationData={setEducationData}
              setShow={setShowEducationForm}
              show={showEducationForm}
            />
          ) : (
            <Grid
              container
              direction="row"
              justify="space-between"
              alignItems="center"
              item
              xs={12}
              className="px-3"
            >
              <Grid item className="mr-auto">
                <span>Add {educationData.length ? "more" : ""} education</span>
              </Grid>
              <Grid item className="ml-auto">
                <Tooltip title="Add Education">
                  <Fab
                    size="small"
                    color="primary"
                    aria-label="add"
                    onClick={() => {
                      setShowEducationForm(!showEducationForm);
                    }}
                  >
                    <AddIcon />
                  </Fab>
                </Tooltip>
              </Grid>
            </Grid>
          )}
          {educationData.length ? (
            <Grid
              item
              xs={12}
              style={{ textAlign: "right" }}
              className="mt-4 px-3"
            >
              <Button
                color="primary"
                variant="contained"
                size="small"
                startIcon={<Save />}
                onClick={saveEducation}
              >
                Save
              </Button>
            </Grid>
          ) : (
            <></>
          )}
        </Grid>
        <Snackbar
          open={showMoveUp}
          autoHideDuration={2000}
          onClose={handleUpClose}
        >
          <Alert onClose={handleUpClose} severity="warning">
            This is already on top !
          </Alert>
        </Snackbar>
        <Snackbar
          open={showMoveDown}
          autoHideDuration={2000}
          onClose={handleDownClose}
        >
          <Alert onClose={handleDownClose} severity="warning">
            This is already on bottom !
          </Alert>
        </Snackbar>
      </Grid>
      </Grid>

    </>
  );
};
export default EducationForm;
