import {
  Backdrop,
  Button,
  CircularProgress,
  FormControl,
  Grid,
  IconButton,
  Input,
  InputAdornment,
  InputLabel,
  Snackbar,
  Tooltip,
} from "@material-ui/core";
import LockIcon from "@material-ui/icons/Lock";
import React, { useEffect, useState } from "react";
import { requestLogin } from "../redux/actions/actions";
import { useDispatch, useSelector } from "react-redux";
import MuiAlert from "@material-ui/lab/Alert";
import Loader from "../components/Loader";
import { Visibility, VisibilityOff } from "@material-ui/icons";

function Alert(props) {
  return <MuiAlert elevation={6} variant="filled" {...props} />;
}

const Login = ({openLogin,setOpenLogin}) => {
  const { loading, errorMessage, errorStatus } = useSelector(
    (state) => state.login
  );
  const dispatch = useDispatch();
  const [password, setPassword] = useState("");
  const [username, setUsername] = useState("");
  const [openAlert, setOpenAlert] = useState(errorStatus);
  const [showPassword, setShowPassword] = useState(false);

  const handleSubmit = (event) => {
    event.preventDefault();
    dispatch(requestLogin({ username, password }));
    setOpenAlert(errorStatus);
    event.target.reset();
  };
  const handleClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }
    setOpenAlert(false);
  };
  const handleClickShowPassword = () => {
    setShowPassword(!showPassword);
  };

  const handleMouseDownPassword = (event) => {
    event.preventDefault();
  };

  return (
    <>
      <Grid
        container
        direction="row"
        justify="center"
        alignItems="center"
        className="bg-light"
        id="login-section"
      >
        <Grid
          container
          item
          id="login-inner-section"
          md={10}
          style={{ borderRadius: "5px" }}
          direction="row"
          justify="center"
          alignItems="center"
        >
          <Grid item md={4} className="text-center">
            <LockIcon style={{ fontSize: "50px" }} />
            <p style={{ width: "50%" }} className="mx-auto fw-bold fs-5">
              Upgrade your portfolio, add new Skills
            </p>
          </Grid>
          <Grid
            component="form"
            container
            direction="row"
            justify="center"
            alignItems="center"
            item
            md={8}
            onSubmit={handleSubmit}
          >
            <Grid item xs={12} className="mt-5 mb-2 fs-3 text-center fw-bolder">
              Log in{" "}
            </Grid>
            <Grid item xs={9} className="my-3">
              <FormControl className="w-100" required>
                <InputLabel htmlFor="login-username">Username</InputLabel>
                <Input
                  id="login-username"
                  type="email"
                  value={username}
                  onChange={(event) => setUsername(event.target.value)}
                  aria-describedby="login-username-text"
                />
              </FormControl>
            </Grid>
            <Grid item xs={9} className="my-3">
              <FormControl className="w-100" required>
                <InputLabel htmlFor="login-password">Password</InputLabel>
                <Input
                  id="login-password"
                  type={showPassword ? "text" : "password"}
                  value={password}
                  onChange={(event) => setPassword(event.target.value)}
                  aria-describedby="login-password-text"
                  endAdornment={
                    <InputAdornment position="end">
                      <IconButton
                        aria-label="toggle password visibility"
                        onClick={handleClickShowPassword}
                        onMouseDown={handleMouseDownPassword}
                      >
                        {showPassword ? <Visibility /> : <VisibilityOff />}
                      </IconButton>
                    </InputAdornment>
                  }
                />
              </FormControl>
            </Grid>
            <Grid item xs={9} style={{ textAlign: "right" }}>
              <Tooltip title="Please connect with contact support">
              <Button className="fw-bold">Forgot Password ?</Button>
              </Tooltip>
            </Grid>
            <Grid
              container
              item
              direction="row"
              justify="flex-end"
              xs={9}
              className="my-5 text-center"
            >
              <Grid
                component={Button}
                type="submit"
                style={{ borderRadius: "none" }}
                item
                xs={4}
                className="login-submit-button fw-bold py-2 fs-6 mx-5"
              >
                Login
              </Grid>
              <Grid
                item
                xs={4}
                component={Button}
                style={{ borderRadius: "none" }}
                className="login-register-button fw-bold py-2 fs-6"
                onClick={()=>{setOpenLogin(!openLogin)}}
                              >
                Create Profile
              </Grid>
            </Grid>
          </Grid>
        </Grid>
      </Grid>
      <Snackbar
        anchorOrigin={{ vertical: "top", horizontal: "center" }}
        open={openAlert}
        autoHideDuration={4000}
        onClose={handleClose}
      >
        <Alert onClose={handleClose} severity="error">
          {errorMessage}
        </Alert>
      </Snackbar>
    </>
  );
};
export default Login;
