import React, { useState } from "react";
import AddCircleIcon from "@material-ui/icons/AddCircle";
import {
  Button,
  FormControl,
  Grid,
  Input,
  InputLabel,
  Select,
} from "@material-ui/core";

const AddSkill = ({ skills, setSkills }) => {
  const [skillName, setSkillName] = useState("");
  const [skillValue, setSkillValue] = useState(0);
  const addSkills = () => {
    setSkills([...skills, { name: skillName, grade: skillValue }]);
    setSkillName("");
    setSkillValue(0);
  };
  return (
    <>
      <Grid container item xs={12} spacing={2}>
        <Grid item xs={4}>
          <FormControl className="intro-form-control" required>
            <InputLabel htmlFor="skill-name">Skill Name</InputLabel>
            <Input
              id="skill-name"
              value={skillName}
              onChange={(event) => setSkillName(event.target.value)}
              aria-describedby="skill-name-text"
            />
          </FormControl>
        </Grid>
        <Grid item xs={4}>
          <FormControl required className="w-100">
            <InputLabel htmlFor="skill-grade">Grade</InputLabel>
            <Select
              native
              onChange={(event) => {
                setSkillValue(event.target.value);
              }}
              inputProps={{
                name: "startingYear",
                id: "skill-grade",
              }}
            >
              <option aria-label="None" value=""></option>
              {[
                { name: "Basic", grade: 20 },
                { name: "Aware", grade: 40 },
                { name: "Hands on", grade: 60 },
                { name: "Proficient", grade: 80 },
              ].map(({ name, grade }) => (
                <option value={grade} key={name}>
                  {name}
                </option>
              ))}
            </Select>
          </FormControl>
        </Grid>
        <Grid item xs={4} className="mt-2" style={{ textAlign: "right" }}>
          <Button
            color="primary"
            variant="contained"
            size="small"
            startIcon={<AddCircleIcon />}
            onClick={addSkills}
          >
            Add Skill
          </Button>
        </Grid>
      </Grid>
    </>
  );
};

export default AddSkill;
