import {
  SET_FEEDBACK_SUCCESS, SET_FEEDBACK_FAILURE, RESET_FEEDBACK,
} from "../actions/actionsTypes";

const initialState = {
  successMessage:false,
  status:false,
  errorMessage:false
};
const errorReducer = (state = initialState, action) => {
  switch (action.type) {
    case SET_FEEDBACK_SUCCESS:{
      return {
        ...state,
        successMessage:action.payload,
        status:true,
        errorMessage:'',
      }
    }
    case SET_FEEDBACK_FAILURE:{
      return {
        ...state,
        successMessage:'',
        status:true,
        errorMessage:action.payload
      }
    }
    case RESET_FEEDBACK:{
      return {
        ...state,
        successMessage:'',
        status:false,
        errorMessage:''
      }
    }
    default:return state;
  }
};
export default errorReducer;
