import {
  Backdrop,
  Button,
  CircularProgress,
  FormControl,
  Grid,
  IconButton,
  Input,
  InputAdornment,
  InputLabel,
  Snackbar,
  Tooltip,
} from "@material-ui/core";
import LockIcon from "@material-ui/icons/Lock";
import React, { useEffect, useState } from "react";
import { requestCreateProfile, requestLogin } from "../redux/actions/actions";
import { useDispatch, useSelector } from "react-redux";
import MuiAlert from "@material-ui/lab/Alert";
import PersonAddIcon from "@material-ui/icons/PersonAdd";
import Loader from "../components/Loader";
import { Visibility, VisibilityOff } from "@material-ui/icons";
function Alert(props) {
  return <MuiAlert elevation={6} variant="filled" {...props} />;
}

const SignUp = ({openLogin,setOpenLogin}) => {
  const { loading, errorMessage, errorStatus } = useSelector(
    (state) => state.login
  );
  const dispatch = useDispatch();
  const [password, setPassword] = useState("");
  const [email, setEmail] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");
  const [username, setUsername] = useState("");
  const [openAlert, setOpenAlert] = useState(errorStatus);
  const [fromError, setFromError] = useState("");
  const [showPassword, setShowPassword] = useState(false);
  const [alreadyProfile, setAlreadyProfile] = useState(false);
  const re = /^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[!_@#\$%\^&\*])(?=.{8,})/;
  const handleSubmit = (event) => {
    event.preventDefault();
    if (
      password &&
      confirmPassword &&
      email &&
      username &&
      confirmPassword === password &&
      password.match(re) &&
      confirmPassword.match(re)
    ) {
      dispatch(requestCreateProfile({email,password,username}));
      setOpenAlert(errorStatus)
    } else if (confirmPassword != password) {
      setFromError("Password doesn't match !");
    }
    event.target.reset();
  };
  const handleClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }
    setOpenAlert(false);
  };
  const handlePassword = (event) => {
    if (event.target.value.match(re)) {
      if (password === confirmPassword) {
        setFromError("Password doesn't match !");
      } else {
        setFromError("");
      }
    } else {
      setFromError("Password is invalid !");
    }
    setPassword(event.target.value);
  };
  const handleConfirmPassword = (event) => {
    if (event.target.value.match(re)) {
      if (password === confirmPassword) {
        setFromError("Password doesn't match !");
      } else {
        setFromError("");
      }
    } else {
      setFromError("Confirm Password is invalid !");
    }
    setConfirmPassword(event.target.value);
  };
  const handleClickShowPassword = () => {
    setShowPassword(!showPassword);
  };

  const handleMouseDownPassword = (event) => {
    event.preventDefault();
  };
  return (
    <>
      <Grid
        container
        direction="row"
        justify="center"
        alignItems="center"
        className="bg-light"
        id="login-section"
      >
        <Grid
          container
          item
          id="login-inner-section"
          md={10}
          style={{ borderRadius: "5px" }}
          direction="row"
          justify="center"
          alignItems="center"
        >
          <Grid item md={4} className="text-center">
            <PersonAddIcon style={{ fontSize: "50px" }} />
            <p style={{ width: "60%" }} className="mx-auto fw-bold fs-5">
              Make your profile and start building your portfolio
            </p>
          </Grid>
          <Grid
            component="form"
            container
            direction="row"
            justify="center"
            alignItems="center"
            item
            md={8}
            onSubmit={handleSubmit}
          >
            <Grid item xs={12} className="mt-5 mb-2 fs-3 text-center fw-bolder">
              Create Profile{" "}
            </Grid>
            <Grid item xs={9} className="my-2">
              <FormControl className="w-100" required>
                <InputLabel htmlFor="register-username">Email</InputLabel>
                <Input
                  id="register-username"
                  type="email"
                  value={email}
                  onChange={(event) => setEmail(event.target.value)}
                  aria-describedby="register-username-text"
                />
                {alreadyProfile && (
                  <span className="text-danger small mt-2">
                    Already have a profile
                  </span>
                )}
              </FormControl>
            </Grid>
            <Grid item xs={9} className="my-2">
              <FormControl className="w-100" required>
                <InputLabel htmlFor="register-name">Name</InputLabel>
                <Input
                  id="register-name"
                  type="text"
                  value={username}
                  onChange={(event) => setUsername(event.target.value)}
                  aria-describedby="register-name-text"
                />
              </FormControl>
            </Grid>

            <Grid item xs={9} className="my-2">
              <Tooltip title="Password should be min 8 digit long alphanumric,with a mixture of uppercase,lowercase,digit and special characters.">
                <FormControl className="w-100" required>
                  <InputLabel htmlFor="login-password">Password</InputLabel>
                  <Input
                    id="login-password"
                    type={showPassword ? "text" : "password"}
                    value={password}
                    onChange={handlePassword}
                    aria-describedby="login-password-text"
                    endAdornment={
                      <InputAdornment position="end">
                        <IconButton
                          aria-label="toggle password visibility"
                          onClick={handleClickShowPassword}
                          onMouseDown={handleMouseDownPassword}
                        >
                          {showPassword ? <Visibility /> : <VisibilityOff />}
                        </IconButton>
                      </InputAdornment>
                    }
                  />
                </FormControl>
              </Tooltip>
            </Grid>

            <Grid item xs={9} className="my-2">
              <Tooltip title="Password should be min 8 digit long alphanumric,with a mixture of uppercase,lowercase,digit and special characters.">
                <FormControl className="w-100" required>
                  <InputLabel htmlFor="login-confirm-password">
                    Confirm Password
                  </InputLabel>
                  <Input
                    id="login-confirm-password"
                    type={showPassword ? "text" : "password"}
                    value={confirmPassword}
                    onChange={handleConfirmPassword}
                    aria-describedby="login-confirm-password-text"
                    endAdornment={
                      <InputAdornment position="end">
                        <IconButton
                          aria-label="toggle password visibility"
                          onClick={handleClickShowPassword}
                          onMouseDown={handleMouseDownPassword}
                        >
                          {showPassword ? <Visibility /> : <VisibilityOff />}
                        </IconButton>
                      </InputAdornment>
                    }
                  />
                </FormControl>
              </Tooltip>
            </Grid>

            <Grid container direction="row" justify="space-between" item xs={9}>
              <Grid
                component={"span"}
                item
                className="fw-bolder small text-danger"
              >
                {fromError}
              </Grid>
              <Grid component={Button} className="fw-bold text-decoration-none" item onClick={()=>setOpenLogin(!openLogin)}>
                Already have an Account
              </Grid>
            </Grid>
            <Grid item xs={9} className="my-5 text-center">
              <Button
                type="submit"
                style={{ borderRadius: "none" }}
                className="login-submit-button fw-bold py-2 fs-6"
              >
                Create Profile
              </Button>
            </Grid>
          </Grid>
        </Grid>
      </Grid>
      <Snackbar
        anchorOrigin={{ vertical: "top", horizontal: "center" }}
        open={openAlert}
        autoHideDuration={4000}
        onClose={handleClose}
      >
        <Alert onClose={handleClose} severity="error">
          {errorMessage}
        </Alert>
      </Snackbar>
    </>
  );
};
export default SignUp;
