import React, { useState } from "react";
import AddCircleIcon from "@material-ui/icons/AddCircle";
import MuiAlert from "@material-ui/lab/Alert";
import {
  Button,
  FormControl,
  Grid,
  Input,
  InputLabel,
  Snackbar,
} from "@material-ui/core";
function Alert(props) {
  return <MuiAlert elevation={6} variant="filled" {...props} />;
}

const AddContactId = ({ contactId, setContactId }) => {
  const [websiteName, setWebsiteName] = useState("");
  const [profileId, setProfileId] = useState("");
  const [openAlert, setOpenAlert] = useState(false);

  const addContact = () => {
    setContactId([...contactId, { name: websiteName, id: profileId }]);
    setWebsiteName("");
    setProfileId("");
  };
  const handleClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }
    setOpenAlert(false);
  };

  return (
    <>
      <Grid
        container
        item
        direction="row"
        justify="space-between"
        alignItems="center"
        xs={12}
        className="my-2"
      >
        <Grid item xs={4}>
          <FormControl className="intro-form-control" required>
            <InputLabel htmlFor="contact-name">Contact Name</InputLabel>
            <Input
              id="contact-name"
              value={websiteName}
              onChange={(event) => setWebsiteName(event.target.value)}
              aria-describedby="contact-name-text"
            />
          </FormControl>
        </Grid>
        <Grid item xs={4}>
          <FormControl required className="w-100">
            <InputLabel htmlFor="profile-id">Profile Id</InputLabel>
            <Input
              id="profile-id"
              value={profileId}
              onChange={(event) => setProfileId(event.target.value)}
              aria-describedby="profile-id-text"
            />
          </FormControl>
        </Grid>
        <Grid item xs={3} className="my-2" style={{ textAlign: "right" }}>
          <Button
            color="primary"
            variant="contained"
            size="small"
            startIcon={<AddCircleIcon />}
            onClick={
              profileId && websiteName
                ? addContact
                : () => {
                    setOpenAlert(!openAlert);
                  }
            }
          >
            Add Contact
          </Button>
        </Grid>
      </Grid>
      <Snackbar open={openAlert} autoHideDuration={2000} onClose={handleClose}>
        <Alert onClose={handleClose} severity="error">
          Some fields are empty !
        </Alert>
      </Snackbar>
    </>
  );
};

export default AddContactId;
