import { Grid, Snackbar } from "@material-ui/core";
import React, { useEffect, useState } from "react";
import FloatingNav from "../components/FloatingNav";
import LeftNav from "../components/LeftNav";
import RightNav from "../components/RightNav";
import ContactIdForm from "../forms/ContactId/ContactIdForm";
import ContactLinkForm from "../forms/ContactLink/ContactLinkForm";
import HeaderForm from "../forms/HeaderForm";
import IntrodunctionForm from "../forms/IntrodunctionForm";
import ProjectForm from "../forms/Project/ProjectFrom";
import ResumeForm from "../forms/ResumeForm";
import MuiAlert from "@material-ui/lab/Alert";

import { useDispatch, useSelector } from "react-redux";
import { requestFeedback, resetFeedback } from "../redux/actions/actions";
import NotVerified from "../components/NotVerified";

function Alert(props) {
  return <MuiAlert elevation={6} variant="filled" {...props} />;
}

const PortfolioForm = () => {
  const { status, successMessage, errorMessage } = useSelector(
    (state) => state.feedback
  );
  const { status:userStatus } = useSelector((state) => state.login);

  const userData = useSelector((state) => state.user.data);
  const [openAlert, setOpenAlert] = useState(status);
  const dispatch = useDispatch();
  const handleClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }
    setOpenAlert(false);
    setTimeout(() => {
      dispatch(requestFeedback());
    }, 1000);
  };
  useEffect(() => {
    setOpenAlert(status);
  }, [status]);
  return (
    <>
      {userStatus?
      <Grid
        container
        direction="row"
        justify="space-around"
        alignItems="center"
      >
        {userData.email && (
          <>
            <LeftNav />
            <RightNav />
            <FloatingNav />
            <IntrodunctionForm />
            <HeaderForm />
            <ResumeForm />
            <ProjectForm />
            <ContactLinkForm />
            <ContactIdForm />
          </>
        )}
      </Grid>:<NotVerified />}
      <Snackbar open={openAlert} autoHideDuration={2000} onClose={handleClose}>
        <Alert
          onClose={handleClose}
          severity={successMessage ? "success" : "error"}
        >
          {successMessage ? successMessage : errorMessage}
        </Alert>
      </Snackbar>
    </>
  );
};

export default PortfolioForm;
