import React, { useState } from "react";
import { Button, Divider, Grid } from "@material-ui/core";
import DeleteIcon from "@material-ui/icons/Delete";
import AddContactId from "./AddContactId";
import { Save } from "@material-ui/icons";
import { useDispatch, useSelector } from "react-redux";
import { requestSaveContact } from "../../redux/actions/actions";

const ContactIdForm = () => {
  const data = useSelector((state) => state.user.data.contact);
  const dispatch = useDispatch();
  const [contactId, setContactId] = useState(data);
  const removeContactId = (index) => {
    let temp = [...contactId];
    temp.splice(index, 1);
    setContactId([...temp]);
  };
  const saveContactId = () => {
    dispatch(requestSaveContact(contactId));
  };
  return (
    <>
      <Grid item container xs={11}>
        <Grid
          container
          direction="row"
          justify="space-around"
          alignItems="flex-start"
          item
          xs={11}
          md={6}
          id="port-id"
          className="border rounded p-3 mx-auto my-3"
        >
          <Grid item xs={12} className="h2 m-3">
            Contact Details
          </Grid>
          <Grid item xs={12}>
            {contactId.length ? (
              <table className="table table-sm small">
                <thead>
                  <tr>
                    <th>Social Media Name</th>
                    <th style={{ textAlign: "center" }}>Profile Id</th>
                    <th style={{ textAlign: "right" }}>Remove Profile</th>
                  </tr>
                </thead>
                <tbody style={{ verticalAlign: "middle  " }}>
                  {contactId.map(({ name, id }, index) => (
                    <tr key={index}>
                      <td>{name}</td>
                      <td style={{ textAlign: "center" }}>{id}</td>
                      <td style={{ textAlign: "right" }}>
                        {" "}
                        <Button
                          className="bg-danger text-white fw-bold"
                          size="small"
                          onClick={() => {
                            removeContactId(index);
                          }}
                          startIcon={<DeleteIcon />}
                        >
                          Delete
                        </Button>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            ) : (
              <>
                <p className="text-center">
                  Add Some Contact Id's like mobile,profile Id, to get touch.
                </p>
                <Divider style={{ width: "90%" }} className="bg-dark mx-auto" />
              </>
            )}
          </Grid>
          <Grid
            container
            item
            direction="column"
            justify="flex-end"
            alignItems="flex-end"
            xs={12}
          >
            <AddContactId contactId={contactId} setContactId={setContactId} />
            {contactId.length ? (
              <Grid
                item
                xs={12}
                style={{ textAlign: "right" }}
                className="mt-4"
              >
                <Button
                  color="primary"
                  variant="contained"
                  size="small"
                  startIcon={<Save />}
                  className="mx-5"
                  onClick={saveContactId}
                >
                  Save
                </Button>
              </Grid>
            ) : (
              <></>
            )}
          </Grid>
        </Grid>
      </Grid>
    </>
  );
};
export default ContactIdForm;
