import React, { useEffect, useState } from "react";

import { Button, Snackbar } from "@material-ui/core";
import FileCopyOutlinedIcon from "@material-ui/icons/FileCopyOutlined";
import DeployModal from "./DeployModal";
import { useDispatch } from "react-redux";
import { requestGitUrl } from "../redux/actions/actions";
import MuiAlert from "@material-ui/lab/Alert";
import { FRONT_END_URL } from "../config";

function Alert(props) {
  return <MuiAlert elevation={6} variant="filled" {...props} />;
}

const RightNav = () => {
  const [open, setOpen] = useState(false);
  const [openAlert, setOpenAlert] = useState(false);
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(requestGitUrl());
  }, []);
  const handleClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }
    setOpenAlert(false);
  };

  function copyToClipboard() {
    let message = `${FRONT_END_URL}${localStorage.getItem('auth')}`
    var textArea = document.getElementById("copy-text");
    textArea.value = message;
    textArea.focus();
    textArea.select();
    try {
      var successful = document.execCommand("copy");
      var msg = successful ? "successful" : "unsuccessful";
      setOpenAlert(true)
    } catch (err) {
      console.log(err.message)
    }
  }
  return (
    <>
      <div
        className="position-fixed row"
        id="port-rightnav"
        style={{ top: "40px", right: "60px" }}
      >
        <div
          style={{ borderBottomLeftRadius: "10px" }}
          className="col-8 offset-4 top-label p-2 px-3 text-center"
        >
          Shareable link
        </div>
        <div className="text-center col-12 my-3">
          <div className="my-2">
            Copy your portfolio link <br />
            in your clipboard
          </div>
          <Button
            startIcon={<FileCopyOutlinedIcon />}
            onClick={copyToClipboard}
            className="bg-primary px-4"
          >
            Copy Link
          </Button>
        </div>
      </div>
      <DeployModal open={open} setOpen={setOpen} />
      <Snackbar open={openAlert} autoHideDuration={2000} onClose={handleClose}>
        <Alert onClose={handleClose} severity="success">
          Linked Copied to Clipboard !
        </Alert>
      </Snackbar>
    </>
  );
};
export default RightNav;
