import {
  call,
  put,
  takeLatest,
  takeEvery,
  all,
} from "@redux-saga/core/effects";

import {
  failureUserData,
  successUserData,
  setResume,
  setIntroduction,
  setHeader,
  setEducation,
  setEmployment,
  setGeneralSkills,
  setProjects,
  setTemp,
  setAddProject,
  setSaveProfiles,
  setSaveContact,
  setFeedbackSuccess,
  setFeedbackFailure,
  resetFeedback,
  successDeploy,
  successGitUrl,
  failureGitUrl,
} from "../actions/actions";
import {
  REQUEST_ADD_PROJECT,
  REQUEST_DELETE_PROJECT,
  REQUEST_DEPLOY,
  REQUEST_FEEDBACK,
  REQUEST_GIT_URL,
  REQUEST_SAVE_CONTACT,
  REQUEST_SAVE_EDUCATION,
  REQUEST_SAVE_EMPLOYMENT,
  REQUEST_SAVE_HEADER,
  REQUEST_SAVE_INTRODUCTION,
  REQUEST_SAVE_PROFILES,
  REQUEST_SAVE_PROJECTS,
  REQUEST_SAVE_RESUME,
  REQUEST_SAVE_SKILLS,
  REQUEST_TEMP,
  REQUEST_USER_DATA,
  RESET_FEEDBACK,
} from "../actions/actionsTypes";
import {
  fetchUserDetails,
  saveProjects,
  fetchTemp,
  addProject,
  updateIntroduction,
  updateHeader,
  saveEducation,
  saveEmployment,
  saveSkills,
  saveProfiles,
  saveContact,
  deleteProject,
  deployPortfolio,
  gitUrl,
} from "../api";

export function* handleTemp() {
  try {
    const userDetails = yield call(fetchTemp);
    yield put(setTemp(userDetails));
  } catch (error) {
    yield put(setTemp(["a"]));
  }
}
export function* handleAddProject(action) {
  try {
    const projectDetails = yield call(addProject, action.payload);
    yield all([
      put(setProjects(projectDetails)),
      put(setFeedbackSuccess("Project added !")),
    ]);
  } catch (error) {
    yield all([
      put(setProjects({})),
      put(setFeedbackFailure("Oops ! Unable to add Proejcts.")),
    ]);
  }
}
export function* handleRequestUser() {
  try {
    const userDetails = yield call(fetchUserDetails);
    yield put(successUserData(userDetails));
  } catch (error) {
    yield put(failureUserData(error.message));
  }
}
export function* handleIntroduction(action) {
  try {
    const introDetails = yield call(updateIntroduction, action.payload);
    yield all([
      put(setIntroduction(introDetails)),
      put(setFeedbackSuccess("Introduction has been updated !")),
    ]);
  } catch (error) {
    yield all([
      put(setIntroduction({})),
      put(setFeedbackFailure("There is something wrong !")),
    ]);
  }
}
