export function* handleSaveProject(action) {
  try {
    const updateProjects = yield call(saveProjects, action.payload);
    yield all([
      put(setProjects(action.payload)),
      put(setFeedbackSuccess("Project Added Successfully !")),
    ]);
  } catch (error) {
    yield put(setFeedbackFailure("There is Something Wrong !"))
  }
}
