import React, { useState } from "react";
import {
  Button,
  FormControl,
  Grid,
  Input,
  InputLabel,
  Snackbar,
} from "@material-ui/core";
import MuiAlert from "@material-ui/lab/Alert";
import AddCircleIcon from "@material-ui/icons/AddCircle";
function Alert(props) {
  return <MuiAlert elevation={6} variant="filled" {...props} />;
}
const AddEmploymentForm = ({
  employmentData,
  setEmploymentData,
  show,
  setShow,
}) => {
  const [companyName, setCompanyName] = useState("");
  const [employmentPosition, setEmploymentPosition] = useState("");
  const [employmentAbout, setEmploymentAbout] = useState("");
  const [openAlert, setOpenAlert] = useState(false);
  const [employmentStartMonth, setEmploymentStartMonth] = useState("");
  const [employmentEndMonth, setEmploymentEndMonth] = useState("");
  const months = [
    "JAN",
    "FEB",
    "MAR",
    "APR",
    "MAY",
    "JUN",
    "JUL",
    "AUG",
    "SEP",
    "OCT",
    "NOV",
    "DEC",
  ];
  const addEducation = () => {
    setEmploymentData([
      ...employmentData,
      {
        name: `${companyName},(${employmentPosition})`,
        start: `${months[parseInt(employmentStartMonth.split("-")[1]) - 1]}-${
          employmentStartMonth.split("-")[0]
        }`,
        end: `${months[parseInt(employmentEndMonth.split("-")[1]) - 1]}-${
          employmentEndMonth.split("-")[0]
        }`,
        about: employmentAbout,
      },
    ]);
    setShow(!show);
  };
  const handleClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }
    setOpenAlert(false);
  };

  return (
    <>
      <Grid item xs={11} md={6} className="my-2">
        <FormControl className="intro-form-control" required>
          <InputLabel htmlFor="education-place">Company Name</InputLabel>
          <Input
            id="education-place"
            value={companyName}
            onChange={(event) => setCompanyName(event.target.value)}
            aria-describedby="education-place-text"
          />
        </FormControl>
      </Grid>
      <Grid item xs={11} md={5} className="my-2">
        <FormControl className="intro-form-control" required>
          <InputLabel htmlFor="employment-position">
            Employment Position
          </InputLabel>
          <Input
            id="employment-position"
            value={employmentPosition}
            onChange={(event) => setEmploymentPosition(event.target.value)}
            aria-describedby="employment-position-text"
          />
        </FormControl>
      </Grid>
      <Grid item xs={11} md={12} className="my-2">
        <FormControl className="intro-form-control" required>
          <InputLabel htmlFor="education-about">About Education</InputLabel>
          <Input
            id="education-about"
            value={employmentAbout}
            onChange={(event) => setEmploymentAbout(event.target.value)}
            aria-describedby="education-place-text"
          />
        </FormControl>
      </Grid>
      <Grid item xs={6} md={3} className="my-2">
        <FormControl className="intro-form-control" required>
          <InputLabel htmlFor="employment-startMonth">
            Starting Month
          </InputLabel>
          <Input
            id="employment-startMonth"
            type="month"
            value={employmentStartMonth}
            onChange={(event) => setEmploymentStartMonth(event.target.value)}
            aria-describedby="employment-startMonth-text"
          />
        </FormControl>
      </Grid>
      <Grid item xs={6} md={3} className="my-2">
        <FormControl className="intro-form-control" required>
          <InputLabel htmlFor="employment-endMonth">Ending Month</InputLabel>
          <Input
            id="employment-endMonth"
            type="month"
            value={employmentEndMonth}
            onChange={(event) => setEmploymentEndMonth(event.target.value)}
            aria-describedby="employment-endMonth-text"
          />
        </FormControl>
      </Grid>
      <Grid item xs={12} md={5} className="mt-2" style={{ textAlign: "right" }}>
        <Button
          color="primary"
          variant="contained"
          size="small"
          startIcon={<AddCircleIcon />}
          onClick={
            employmentAbout &&
            companyName &&
            employmentPosition &&
            employmentStartMonth &&
            employmentStartMonth
              ? addEducation
              : () => {
                  setOpenAlert(!openAlert);
                }
          }
        >
          Add Education
        </Button>
      </Grid>
      <Snackbar open={openAlert} autoHideDuration={2000} onClose={handleClose}>
        <Alert onClose={handleClose} severity="error">
          Some fields are empty !
        </Alert>
      </Snackbar>
    </>
  );
};
export default AddEmploymentForm;

