import React from "react";
import { withStyles } from "@material-ui/core/styles";
import Dialog from "@material-ui/core/Dialog";
import MuiDialogContent from "@material-ui/core/DialogContent";
import Typography from "@material-ui/core/Typography";
import AssignmentIndIcon from "@material-ui/icons/AssignmentInd";
import { IconButton } from "@material-ui/core";
const DialogContent = withStyles((theme) => ({
  root: {
    padding: theme.spacing(2),
  },
}))(MuiDialogContent);

const LargeScreenDialoge = () => {
  return (
    <>
      <div>
        <Dialog aria-labelledby="customized-dialog-title" open={true}>
          <DialogContent className="text-center" dividers>
            <div>
              <AssignmentIndIcon style={{ fontSize: 70 }} />
            </div>
            <div className="h3 my-2">Portfolio Builder</div>
            <Typography gutterBottom>
              Choose large Screen device like laptop,tablets.
            </Typography>
          </DialogContent>
        </Dialog>
      </div>
    </>
  );
};
export default LargeScreenDialoge;
