import {
  FAILURE_DEPLOY,
  FAILURE_GIT_URL,
  FAILURE_USER_DATA,
  REQUEST_DEPLOY,
  REQUEST_GIT_URL,
  REQUEST_USER_DATA,
  SET_ADD_PROJECT,
  SET_SAVE_CONTACT,
  SET_SAVE_EDUCATION,
  SET_SAVE_EMPLYOMENT,
  SET_SAVE_HEADER,
  SET_SAVE_INTRODUCTION,
  SET_SAVE_PROFILES,
  SET_SAVE_PROJECTS,
  SET_SAVE_SKILLS,
  SET_TEMP,
  SUCCESS_DEPLOY,
  SUCCESS_GIT_URL,
  SUCCESS_USER_DATA,
} from "../actions/actionsTypes";

const initialState = {
  data: {},
  loading: false,
  error: "",
  temp: [],
  giturl: "",
  deploymentStatus: 0,
};
const userReducer = (state = initialState, action) => {
  switch (action.type) {
    case REQUEST_USER_DATA: {
      return {
        ...state,
        loading: true,
      };
    }
    case SUCCESS_USER_DATA: {
      return {
        ...state,
        data: action.payload,
        loading: false,
      };
    }
    case FAILURE_USER_DATA: {
      return {
        ...state,
        data: {},
        loading: false,
        error: action.payload,
      };
    }
    case SET_SAVE_HEADER: {
      return {
        ...state,
        data: {
          ...state.data,
          resumeFile: action.payload.resumeFile,
          about: action.payload.about,
          resume:{...state.data.resume,header:action.payload.header}
        },
        loading: false,
        error: "",
      };
    }
    case SET_SAVE_INTRODUCTION: {
      return {
        ...state,
        data: { ...state.data, ...action.payload },
      };
    }
    case SET_SAVE_EDUCATION: {
      return {
        ...state,
        data: {
          ...state.data,
          resume: { ...state.data.resume, education: [...action.payload] },
        },
      };
    }
    case SET_SAVE_EMPLYOMENT: {
      return {
        ...state,
        data: {
          ...state.data,
          resume: { ...state.data.resume, employment: [...action.payload] },
        },
      };
    }
    case SET_SAVE_SKILLS: {
      return {
        ...state,
        data: {
          ...state.data,
          resume: { ...state.data.resume, generalSkills: [...action.payload] },
        },
      };
    }
    case SET_SAVE_PROJECTS: {
      return {
        ...state,
        data: { ...state.data, projects: [...action.payload] },
      };
    }
    case SET_ADD_PROJECT: {
      return {
        ...state,
        data: {
          ...state.data,
          projects: [...state.data.projects, action.payload],
        },
      };
    }
    case SET_SAVE_PROFILES: {
      return {
        ...state,
        data: { ...state.data, contactLinks: [...action.payload] },
      };
    }
    case SET_SAVE_CONTACT: {
      return {
        ...state,
        data: { ...state.data, contact: [...action.payload] },
      };
    }
    case SET_TEMP: {
      return {
        ...state,
        temp: [...action.payload],
      };
    }
    case REQUEST_DEPLOY: {
      return {
        ...state,
        loading: true,
      };
    }
    case SUCCESS_DEPLOY: {
      return {
        ...state,
        loading: false,
        giturl: action.payload.giturl,
        deploymentStatus: action.payload.status,
      };
    }

    case FAILURE_DEPLOY: {
      return {
        ...state,
        loading: false,
      };
    }
    case REQUEST_GIT_URL: {
      return {
        ...state,
      };
    }
    case SUCCESS_GIT_URL: {
      return {
        ...state,
        giturl: action.payload.giturl,
        deploymentStatus: action.payload.status,
      };
    }
    case FAILURE_GIT_URL: {
      return {
        ...state,
      };
    }
    default:
      return state;
  }
};
export default userReducer;
